.card-container {
    display: flex;
    flex-direction: column;
    background-color: #95dada;
    border: 1px solid grey;
    border-radius: 1vw;
    padding: 1vw;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    width: 18vw;
    height: auto;

}

.card-container:hover {
    transform: scale(1.05) ;
}

.image{
    width: 18vw;
    height: auto;
}

.text-1{
    font-size:2vw
 }

 .text-2{
    font-size:1vw
 }